import {
    REQUEST,
    START,
    FAILED,
    RECEIVE,
    RATE,
    BANKS
} from '../constants';

const initialState = {
    isInvalid: false,
    isLoading: false
};

export default (state = initialState, action) => {

    const {type, payload} = action

    switch (type) {
        /*case REQUEST + START:
            return {
                ...state,
                isInvalid: false,
                isLoading: true
            }*/
        case RECEIVE + BANKS:
            return {
                ...state,
                isInvalid: false,
                current: payload,
                isLoading: false
            }

      /*  case RECEIVE + BUDGET:
            return {
                ...state,
                isInvalid: false,
                current: payload,
                isLoading: false
            }
        case REQUEST + FAILED:
            return {
                ...state,
                isInvalid: true,
                error: payload
            }*/

        default:
            return state;
    }
};





